<script>
    import Button from '../components/Button.svelte'
    import Input from '../components/Input.svelte'    
	import {userbaseStore, userStore, promiseStore} from '../stores'

    let username, password, rememberMe, checked
    checked = false
    rememberMe = "none"
        
    function signin() {
        $promiseStore = $userbaseStore.signIn({username, password, rememberMe})
            .then((user) => $userStore = user)
    }
</script>
<div class="container-xxl position-relative bg-white d-flex p-0">
    <!-- Spinner Start -->
    <div id="spinner" class="bg-white position-fixed translate-middle w-100 vh-100 top-50 start-50 d-flex align-items-center justify-content-center">
        <div class="spinner-border text-primary" style="width: 3rem; height: 3rem;" role="status">
            <span class="sr-only">Loading...</span>
        </div>
    </div>
    <!-- Spinner End -->


    <!-- Sign Up Start -->
    <div class="container-fluid">
        <div class="row h-100 align-items-center justify-content-center" style="min-height: 100vh;">
            <form on:submit|preventDefault={signin} class="col-12 col-sm-8 col-md-6 col-lg-5 col-xl-4">
                <div class="bg-light rounded p-4 p-sm-5 my-4 mx-3">
                    <div class="d-flex align-items-center justify-content-between mb-3">
                        <a href="index.html" class="">
                            <h3 class="text-primary"><i class="fa fa-hashtag me-2"></i>UCOFFEE</h3>
                        </a>
                        <h3>Sign In</h3>
                    </div>
                    <Input label="Username" bind:value={username} type="text" required />
                    <Input label="Password" bind:value={password} type="password" required />
                    <Button>Sign In!</Button>
                    <div class="d-flex align-items-center justify-content-between mb-4">
                        <div class="form-check">
                            <input {checked} on:change={e => { (e.target.checked) ? rememberMe = "local": rememberMe = "none"}} type="checkbox" class="form-check-input" id="exampleCheck1">
                            <label class="form-check-label" for="exampleCheck1">Check me out</label>
                        </div>
                        <a href="/#/forgotpassword">Forgot Password</a>
                    </div>
                </div>
            </form>
        </div>
    </div>
    <!-- Sign Up End -->
</div>