<script>
	import Router from 'svelte-spa-router'
	import {wrap} from 'svelte-spa-router/wrap';
	
	import Dashmin from './Dashmin.svelte'
	import SignUp from './routes/SignUp.svelte'
	import SignIn from  './routes/SignIn.svelte'
	import ForgotPassword from  './routes/ForgotPassword.svelte'
	import NotFound from './routes/NotFound.svelte'
    import Loading from './components/Loading.svelte'
	import '../public/build/src/css/reset.css'
	import '../public/build/src/css/style.css'

	import {userbaseStore, userStore, promiseStore} from './stores'
	import Admin from './Admin.svelte'

	const routes = {
		'/' : wrap({
			asyncComponent: () => SignIn,
			loadingComponent: Loading,
		}),
		'/signup' : SignUp,
		'/forgotpassword' : ForgotPassword,
		'*': NotFound
	}

	const userbase = window.userbase
	window.userbase = null

	$userbaseStore = userbase
	$userStore = null


	$promiseStore = userbase.init({appId: '89416b7e-6a19-4e82-95a1-63f0f7589a73'})
		.then((session) => $userStore = session.user)

</script>

<Dashmin />
{#await $promiseStore}

	<Loading />
	
	{:then _}
		{#if $userStore} 
			<!-- Hello, {$userStore.username}! -->
			<Admin />
		{:else}
			<Router {routes}/>
		{/if}
	{:catch error}
			<strong>ERROR! {error}</strong>
{/await}